.Head {
  position: relative;
  height: 100%;
  background: rgb(0, 146, 255);
  background: rgb(0, 146, 255);
  background: linear-gradient(
    170deg,
    rgba(0, 146, 255, 1) 50%,
    rgba(2, 0, 36, 1) 50%,
    rgba(0, 125, 255, 1) 50%,
    rgba(20, 20, 20, 1) 50%
  );
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  cursor: default;
  overflow: hidden;
}

.Head::after {
  content: "";
  position: absolute;
  top: 50%;
  left: -26%;
  transform: translateX(50%);
  z-index: 0;
  transform: rotate(170deg);
  height: 10px;
  width: 150%;
  background: rgb(0, 146, 255);
}

.Head-person {
  position: relative;
  z-index: 1;
  background: white;
  margin-top: 200px;
  border-radius: 50%;
  border: 5px solid #ffffff;
  width: 250px;
  height: 250px;
  text-align: center;
  -webkit-box-shadow: -8px 10px 30px -5px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -8px 10px 30px -5px rgba(0, 0, 0, 0.75);
  box-shadow: -8px 10px 30px -5px rgba(0, 0, 0, 0.75);
  margin: 0;
  justify-self: center;
  align-self: center;
  overflow: hidden;
}

.Head-person img {
  width: 100%;
  filter: grayscale(100%);
  transition: all 0.3s ease;
}
.Head-person:hover img {
  filter: grayscale(0%);
}

.Head-icons {
  font-size: 15rem;
  color: rgb(20, 20, 20);
}

.Head-info {
  color: white;
  font-family: "Source Code Pro", monospace;
  margin: 5px 0 0 0;
  text-align: center;
  width: 60%;
  justify-self: center;
  align-self: center;
}

.Head-info-name {
  font-size: 2rem;
}

.Head-info-desc {
  font-size: 0.9rem;
}
