.Skills {
  position: relative;
  background: rgba(20, 20, 20, 1);
}

.Skills h2 {
  text-align: center;
  margin: 0;
  padding: 50px;
  color: #ffffff;
  font-family: "Source Code Pro", monospace;
}

.SkillsAll {
  background: rgba(20, 20, 20, 1);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.Skills-Button {
  position: relative;
  font-family: "Source Code Pro", monospace;
  font-size: 1.2em;
  background: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
  color: #ffffff;
  display: block;
  width: 150px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
  font-weight: 600;
}

.arrow {
  border: solid #fff;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
  margin: 1px 0 2px 0;
  transition: 0.3s ease;
}

.down {
  transition: 0.3s ease;
  transform: rotate(45deg);
}

.up {
  transform: rotate(-135deg);
}
