.Projects-Single {
  margin: 50px;
  max-width: 25%;
  text-align: center;
  padding: 0.2rem;
  background-color: #20bf55;
  background: linear-gradient(315deg, #20bf55 0%, rgb(0, 146, 255) 74%);
}

.InnerProjects-Single {
  padding: 2rem;
  background: rgba(20, 20, 20, 1);
}

.Projects-Single h3 {
  font-size: 1.5em;
}

.Projects-Single img {
  width: 60%;
}

.Projects-Single-anchor {
  display: inline-block;
  text-align: center;
  padding: 10px;
  width: 150px;
  background: rgb(0, 146, 255);

  text-decoration: none;
  color: white;
}
