.SkillsMore {
  overflow: hidden;
}

.More-Show {
  width: 60%;
  display: block;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  transition: 0.3s ease;
}

.More-Hide {
  height: 40px;
  overflow: hidden;
  transition: 0.3s ease;
}

@media screen and (max-width: 768px) {
  .More-Show {
    width: 90%;
  }
}
