.Bio {
  position: relative;
  display: block;
  width: 100%;
  padding-top: 50px;
  text-align: center;
  background: rgba(20, 20, 20, 1);
  font-family: "Source Code Pro", monospace;
}

.Bio-heading {
  margin: 0;
  text-align: center;
  color: white;
}

.Bio p {
  margin: 0 50px 50px 50px;
  color: white;
  display: block;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
}

.Bio p::before,
.Bio p::after {
  line-height: 20px;
  content: '"';
  font-size: 2em;
}
