.Navbar {
  align-self: flex-start;
  margin-bottom: 5%;
  padding: 10px;
}

.Navbar a {
  font-size: 1.1rem;
  font-weight: bold;
  margin: 0 10px;
  width: 10px;
  text-decoration: none;
  color: rgb(255, 255, 255);
  font-family: "Quicksand", sans-serif;
  trainsition: 0.3s ease;
}

.active::before {
  content: "";
  position: absolute;
  z-index: 100;
  top: 2rem;
  width: inherit;
  height: 3px;
  background-color: aliceblue;
}

.Navbar-active {
  color: white;
}
